.flex__start {
	display: flex;
	justify-content: flex-start;
	align-items: center;
}

.flex__center {
	display: flex;
	justify-content: center;
	align-items: center;
}

.flex__between {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.flex__column {
	display: flex;
	flex-direction: column;
}

